const FullDataset = () => {
    const arr =
        [{ "name": "USA", "count": 201, "list": [{ "name": "San Francisco", "count": 18 }, { "name": "Cambridge", "count": 2 }, { "name": "New York", "count": 35 }, { "name": "Los Angeles", "count": 10 }, { "name": "Philadelphia", "count": 4 }, { "name": "Seattle", "count": 6 }, { "name": "Boston", "count": 3 }, { "name": "Arlington", "count": 1 }, { "name": "Roseville", "count": 1 }, { "name": "Ithaca", "count": 1 }, { "name": "Phoenix", "count": 2 }, { "name": "New York City", "count": 2 }, { "name": "Morrow", "count": 1 }, { "name": "Cleveland", "count": 2 }, { "name": "Washington", "count": 3 }, { "name": "Salt Lake City", "count": 1 }, { "name": "Atlanta", "count": 5 }, { "name": "Chicago", "count": 13 }, { "name": "Hackensack", "count": 1 }, { "name": "Claremont", "count": 1 }, { "name": "Brooklyn", "count": 2 }, { "name": "Camden County", "count": 1 }, { "name": "Peoria", "count": 1 }, { "name": "Tucson", "count": 1 }, { "name": "Sunnyvale", "count": 1 }, { "name": "Durham", "count": 1 }, { "name": "Charlotte", "count": 1 }, { "name": "Clearwater", "count": 1 }, { "name": "Orlando", "count": 3 }, { "name": "New Jersey", "count": 1 }, { "name": "Minneapolis", "count": 1 }, { "name": "Biloxi", "count": 1 }, { "name": "Austin", "count": 9 }, { "name": "Roseburg", "count": 1 }, { "name": "Amherst", "count": 1 }, { "name": "Glendale", "count": 1 }, { "name": "Pittsburgh", "count": 2 }, { "name": "Greensboro", "count": 2 }, { "name": "Hartford", "count": 1 }, { "name": "Madison", "count": 2 }, { "name": "NYC", "count": 1 }, { "name": "Columbus", "count": 1 }, { "name": "Nashville", "count": 4 }, { "name": "Berkeley", "count": 5 }, { "name": "Baltimore", "count": 2 }, { "name": "Urbana", "count": 2 }, { "name": "Pacifica", "count": 1 }, { "name": "Roanoke", "count": 1 }, { "name": "Raleigh", "count": 1 }, { "name": "Bellevue", "count": 2 }, { "name": "Jersey City", "count": 1 }, { "name": "Newport News", "count": 1 }, { "name": "Vienna", "count": 1 }, { "name": "Las Vegas", "count": 1 }, { "name": "Daytona Beach", "count": 1 }, { "name": "Houston", "count": 1 }, { "name": "East Lansing", "count": 1 }, { "name": "Dallas", "count": 1 }, { "name": "Fargo", "count": 1 }, { "name": "Broken Arrow", "count": 1 }, { "name": "Columbia", "count": 2 }, { "name": "Sanford", "count": 1 }, { "name": "Fair Oaks", "count": 1 }, { "name": "Excelsior", "count": 1 }, { "name": "Charlottesville", "count": 1 }, { "name": "Orange", "count": 1 }, { "name": "Palo Alto", "count": 1 }, { "name": "Somerville", "count": 1 }, { "name": "Cincinnati", "count": 1 }, { "name": "San Diego", "count": 1 }, { "name": "Portland", "count": 1 }, { "name": "Moscow", "count": 1 }, { "name": "Pasadena", "count": 1 }, { "name": "Jacksonville", "count": 1 }, { "name": "Morgantown", "count": 1 }, { "name": "Orange County", "count": 1 }, { "name": "Avon", "count": 1 }, { "name": "Stratford", "count": 1 }, { "name": "West Lafayette", "count": 1 },] }, { "name": "Canada", "count": 12, "list": [{ "name": "Burlington", "count": 1 }, { "name": "Toronto", "count": 6 }, { "name": "Waterloo", "count": 1 }, { "name": "Woodstock", "count": 1 }, { "name": "Vancouver", "count": 1 }, { "name": "London", "count": 2 },] }, { "name": "Guatemala", "count": 1, "list": [] }, { "name": "Hungary", "count": 1, "list": [] }, { "name": "Egypt", "count": 1, "list": [] }, { "name": "Bulgaria", "count": 1, "list": [] }, { "name": "Australia", "count": 3, "list": [{ "name": "Melbourne", "count": 2 }, { "name": "Victoria", "count": 1 },] }, { "name": "United Kingdom", "count": 13, "list": [{ "name": "London", "count": 7 }, { "name": "Bishops Stortford", "count": 1 }, { "name": "Great Ayton", "count": 1 }, { "name": "Bath", "count": 1 }, { "name": "Bristol", "count": 1 }, { "name": "Manchester", "count": 1 }, { "name": "Birmingham", "count": 1 },] }, { "name": "Uruguay", "count": 1, "list": [{ "name": "Montevideo", "count": 1 },] }, { "name": "Switzerland", "count": 2, "list": [{ "name": "Zurich", "count": 2 },] }, { "name": "Germany", "count": 2, "list": [{ "name": "Berlin", "count": 1 },] }, { "name": "Spain", "count": 4, "list": [{ "name": "Madrid", "count": 2 }, { "name": "Valencia", "count": 1 },] }, { "name": "Argentina", "count": 2, "list": [{ "name": "Buenos Aires", "count": 1 },] }, { "name": "Turkey", "count": 2, "list": [{ "name": "Istanbul", "count": 1 }, { "name": "Ankara", "count": 1 },] }, { "name": "Indonesia", "count": 1, "list": [{ "name": "Jakarta", "count": 1 },] }, { "name": "Ireland", "count": 2, "list": [{ "name": "Dublin", "count": 1 }, { "name": "Belfast", "count": 1 },] }, { "name": "India", "count": 2, "list": [{ "name": "Pune", "count": 1 },] }, { "name": "Kenya", "count": 1, "list": [{ "name": "Eldoret", "count": 1 },] }, { "name": "Brazil", "count": 3, "list": [] }, { "name": "Tanzania", "count": 1, "list": [] }, { "name": "Netherlands", "count": 2, "list": [{ "name": "Amsterdam", "count": 1 },] }, { "name": "Belgium", "count": 1, "list": [{ "name": "Brussels", "count": 1 },] }, { "name": "Ukraine", "count": 1, "list": [] }, { "name": "Venezuela", "count": 1, "list": [] }, { "name": "Ghana", "count": 1, "list": [{ "name": "Accra", "count": 1 },] }, { "name": "Chile", "count": 1, "list": [{ "name": "Valparaiso", "count": 1 },] }, { "name": "Tunisia", "count": 1, "list": [{ "name": "Tunis", "count": 1 },] }];

    function _getTotalCount(arr) {
        let count = 0;
        for (var i = 0; i < arr.length; i++) {
            count += arr[i].count;
        }
        return count;
    }

    function _getJsonObj(name, count, order, list) {
        return { "name": name, "count": count, "order": (order || count), "list": list };
    }

    function _normalizeCityArray(arr, cityCountExpected) {
        let auxArr = arr;
        auxArr = auxArr.map(element => {
            return _getJsonObj(element.name, element.count);
        });
        auxArr = _sortByCount(auxArr);
        const count = _getTotalCount(auxArr);
        if (count > cityCountExpected) return { arr: auxArr, error: true };
        if (count === cityCountExpected) return { arr: auxArr, error: false };
        const sub = cityCountExpected - count;
        if (sub === 0) return { arr: auxArr, error: true };
        auxArr.push(_getJsonObj("unknown", sub, -1));
        return { arr: auxArr, error: false };
    }

    function _getBiggest(anArr, limit) {
        const auxArr = [], unknown = [];
        for (var i = 0; i < anArr.length; i++) {
            const count = anArr[i].count;
            if (count >= limit) {
                auxArr.push(_getJsonObj(anArr[i].name, anArr[i].count));
            } else {
                unknown.push(_getJsonObj(anArr[i].name, anArr[i].count));
            }
        }
        const total = _getTotalCount(anArr);
        const count = _getTotalCount(auxArr);
        auxArr.push(_getJsonObj("Others", total - count, "-1", unknown));
        return auxArr;
    }

    function _getCitiesByOwnerName(name) {
        for (var i = 0; i < arr.length; i++) {
            const itemName = arr[i].name;
            if (itemName.toLowerCase() === name.toLowerCase()) {
                return _normalizeCityArray(arr[i].list, arr[i].count);
            }
        }
        return null;
    }

    function _getCountryByName(name) {
        for (var i = 0; i < arr.length; i++) {
            const itemName = arr[i].name;
            if (itemName.toLowerCase() === name.toLowerCase()) {
                return arr[i];
            }
        }
        return null;
    }

    function _hasCityName(countryName, cityName) {
        const cities = _getCitiesByOwnerName(countryName);
        for (var i = 0; i < cities.arr.length; i++) {
            if (cities.arr[i].name.toLowerCase() === cityName.toLowerCase()) {
                return cities.arr[i];
            }
        }
        return null;
    }

    function _hasCountryOnlyUnknownCity(countryName) {
        const country = _getCountryByName(countryName);
        const city = _hasCityName(countryName, 'unknown');
        return city && country && city.count === country.count;
    }

    function _sortByCount(anArr) {
        anArr.sort((a, b) => {
            return a.order > b.order ? -1 : 1;
        })
        return anArr;
    }

    function _getAsideFrom(anArr, ASideFromArr) {
        const auxArr = [];
        for (var i = 0; i < anArr.length; i++) {
            if (ASideFromArr.indexOf(anArr[i].name) === -1) {
                auxArr.push(anArr[i]);
            }
        }
        return auxArr;
    }

    return {
        dataset: arr,
        getCitiesByOwnerName: _getCitiesByOwnerName,
        getCountryByName: _getCountryByName,
        hasCityName: _hasCityName,
        hasCountryOnlyUnknownCity: _hasCountryOnlyUnknownCity,
        sortByCount: _sortByCount,
        getAsideFrom: _getAsideFrom,
        getTotalCount: _getTotalCount,
        getBiggest: _getBiggest,
    };
}

module.exports = FullDataset;


